<template>
<div class="main">
  <h1>Vue XO</h1>
  <input v-model="userName" autocomplete="off" type="text" name="username" id="username" placeholder="Username"><br><br>  
  <input v-model="room" autocomplete="off" type="text" name="room" id="room" placeholder="Room"><br><br>  
  <router-link class="button"
    :to="{ name: 'game', params: { 
        userName: userName,
        room: room
        }}"
  >
  Let's Play!
  </router-link>
</div>
</template>

<script>
export default {
  name: 'mainEl',
  data () {
    return {
      userName: '',
      room: ''
    }
  }
}
</script>

<style>
h1 {
  font-size: 4rem;
  color: white;
}

h1::first-letter {
  color: var(--main-blue);
}

.main {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

input {
  border: 2px solid white;
  background-color: transparent;
  padding: 3px 10px;
  font-size: 1.5rem;
  color: white;
  width: 15%;
  margin-bottom: 2px;
}

input:focus, input:active {
  border-bottom: 4px solid white;
  outline: none;
  margin-bottom: 0px;
}

.button {
  border: 2px solid white;
  background-color: transparent;
  padding: 3px 10px;
  font-size: 1.5rem;
  color: white;
  width: 15%;
  text-align: center;
  text-decoration: none;
}

.button:hover {
  border-bottom: 4px solid white;
}
</style>



